@import 'vars.scss';

.mainContent{
  width: 100%;
  height: calc(100% - 102px);
  margin: 0;
  padding: 0;
  @include flex(center,start);
  flex-direction: column;
  gap: 20px;
  .header{
    width: 100%;
    height: 300px;
    background: linear-gradient( rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7) ), url("../img/office.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top;
    @include flex(center,center);
    .content{
      width: 60%;
      text-align: center;
      h3{
        margin: 0 auto;
        margin-top: 10px;
        font-size: 2.5rem;
        color: white;
      }
    }
  }
  .contentCards{
    width: 60%;
    height: calc(65vh - 162px);
    @include flex(start , center);
    gap: 20px
  }
  .chips{
    width: 60%;

    text-align: center;
    color: #fff;
    h3{
      font-size: 2rem;
      margin-bottom: 10px;
    }
    h4{
      font-size: 1.2rem;
      margin-top: 15px;
      margin-bottom: 15px;

    }
    p{
      width: 50%;
      margin: 0 auto;
      margin-bottom: 10px;
      font-size: 1.1rem;

    }
    .features{
      @include flex(start , center);
      gap: 70px;
      width: 100%;
      margin-bottom: 30px;
      color: #fff;
      .feature{
      margin-top: 40px;

        @include flex(center , center);
        flex-direction: column;
        gap:10px;
        svg{
          fill: aquamarine;
        }
        p{
          width: 240px;
          font-size: 1.1rem;
        }
      }
    }
  }
  .quality{
    margin-top: 10px;
    width: 100%;
    background-color: $cardBgC;
    min-height: 340px;
    @include flex(center,center);
    .content{
      text-align: center;
      color: #fff;
      width: 60%;
      h3{
        margin-top: 0;
        font-size: 2rem;
        margin-bottom: 10px;
      }
      h5{
        margin-top: 10px;
        font-size: 1.3rem;
      }
      p{
        font-size: 1.1rem;
      }
    }
  }
  .interface{
    text-align: center;
    color: $textColor;
    h2{
      font-size: 30px;
    }
  }
  .aboutUs{
    width: 60%;
    @include flex(center , space-between);
    gap: 40px;
    .aboutblock{
      height: 320px;
      text-align: center;
      @include flex(center ,start);
      width: 40%;
      gap: 10px;
      flex-direction: column;
      padding: 20px 0 20px 0;
      h3{
        margin-top: 5px;
        color: #fff;
        font-size:26px;
        margin-bottom: 0px;
      }
      p{
        color:  #fff;
        width: 130px;
      }
      svg{
        width: 60px;
        height: 60px;
        fill:  aquamarine;
      }
    }
  }
  .contactUs{
    height: 200px;
    width: 60%;
    background-color: $cardBgC;
    margin-bottom: 30px;
    border-radius: 0.5rem;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 0.125rem 0.375rem;
    text-align: center;
    h3{
      color:  #fff;
      font-size: 32px;
      margin-bottom: 10px;
    }
    p{
      color:  #fff;
      font-size: 18px;

    }
    a{
      color: aquamarine
    }
  }
}
@media(max-width:'900px') {
  .mainContent{
    .chips{
      p{
        width: 70%;
      }
      .features{
        @include flex(center, center);
        flex-direction: column;
        gap: 40px;
      }
    }
    .quality{
      padding: 20px 0;
    }
    .aboutUs{
      flex-direction: column;
      gap:0;
      .aboutblock{
        height: 270px;
        width: 100%;
        h3{
          width: 100%;
        }
      }
    }
    .contactUs{
      width: 80%;
      height: 100%;
      padding-bottom: 20px;
      padding-left: 10px;
      padding-right: 10px;
    }
  }
}
@media(max-width:'500px') {
  .mainContent{
    .header{
      .content{
        width: 80%;
        h3{
          font-size: 2rem;
        }
      }
    }
    .chips{
      width: 80%;
    }
    .quality{
      padding: 20px 0;
      .content{
        width: 80%;
      }
    }
    .aboutUs{
        .aboutblock{
          height: 100%;
        }
    }
    .contactUs{

    }
  }
}